import React from 'react';
import { createUseStyles } from 'react-jss'

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
});

export const HomePage = ({children, url}) => {
    const classes = useStyles();
    return (
        <h2 className={classes.title}>
            This website is under construction. If you find this, please ignore it.
        </h2>
    );
}
