import React from 'react';
import { createUseStyles } from 'react-jss';
import { Game } from '../../Components/Game';

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
    body: {
        margin: '40px',
        fontSize: '20px'
    },
    link: {
        color: 'cyan'
    },
    details: {
        marginTop: '0px',
        marginBottom: '0px',
    }
});

export const HoloDefenderPage = ({children}) => {
    const classes = useStyles();
    return (
        <div>
            <h2 className={classes.title}>
                Holo Defender
            </h2>
            <Game url="/content/games/HoloDefender/index.html" alt="/content/images/games/holo-defender/background.png"/>
            <div className={classes.body}>
                <p>Holo Defender is an arcade tower defense game originally made for the Microsoft Hololens, and later ported to Windows and web browsers. The goal of this project was to create a simple grid-based tower defense that can be projected into the physical world. It's a standard tower defense, where robot foes move through predetermined paths trying to get to the goal, tp damage damage the player’s home base. Players must use their wits to strategically place a variety of towers onto the map to stop waves of enemies from progressing. However, once the player’s base has taken too much damage, it’s all be over.</p>
                <p>This game was originally developed a team of four students at Neumont University. This project was made for a class about building Software Products for Emerging Platforms. We were allowed to work on pretty much whatever project we wanted, so long as we made it for a new, emerging platform, and so we decided to make a game for the Microsoft Hololens. The main goal was to learn about the platform, and the challenges of developing for it. I later decided to port the project to PC and Web browsers, with a brand new UI, to allow more people to play it.</p>
                <h3 className={classes.details}>Details:</h3>
                <ul className={classes.details}>
                    <li>Platform: Windows, Web Browser, Hololens(formerly)</li>
                    <li>My Roles: Lead Game Designer, Gameplay Programmer, UI Programmer</li>
                    <li>Tools Used: Unity3D, C#, Windows SDK</li>
                    <li>Team Size: 4 people</li>
                    <li>Time Frame: 4 weeks + about a week for ports</li>
                    <li>Downloads: <a className={classes.link} href="/content/games/Windows/HoloDefender.zip">Windows</a></li>
                </ul>
            </div>
        </div>
    );
}
