import React from 'react';
import { createUseStyles } from 'react-jss'

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
    body: {
        margin: '40px',
        fontSize: '20px'
    },
});

export const AboutPage = ({children, url}) => {
    const classes = useStyles();
    return (
        <div>
            <h2 className={classes.title}>
                About Page
            </h2>
            <div className={classes.body}>
                <p>Hi, my name is Josh Stephens. This page will be filled out later.</p>
            </div>
        </div>
    );
}
