import React from 'react';
import { createUseStyles } from 'react-jss';
import { Game } from '../../Components/Game';

const useStyles = createUseStyles({
    title: {
        color: 'white',
        textAlign: 'center'
    },
    body: {
        margin: '40px',
        fontSize: '20px'
    },
    link: {
        color: 'cyan'
    },
    details: {
        marginTop: '0px',
        marginBottom: '0px',
    }
});

export const BurstFightersPage = ({children}) => {
    const classes = useStyles();
    return (
        <div>
            <h2 className={classes.title}>
                Burst Figters
            </h2>
            <Game url="/content/games/BurstFighters/index.html" alt="/content/images/games/burst-fighters/background-alt.png" height="820px" width="460px"/>
            <div className={classes.body}>
                <p>Burst Fighters is a fantasy Physics-based RPG designed for mobile platforms. You must travel through a kingdom collecting powerful coins and artifacts that you can use in battle. Battles take place across a variety of locations; from beautiful beaches, to fiery volcanoes. In battle, you can bring three coins in your party, to fight against other enemy coins. When the time is right, tap the screen to unleash a powerful burst attack.</p>
                <p>This project was developed for a Neumont University Enterprise Project, which is designed to give students real work experience by building and releasing real products</p>
                <h3 className={classes.details}>Controls:</h3>
                <ul className={classes.details}>
                    <li>Tap on a coin, and pull back to aim. Release to shoot. Hit your opponents to deal damage</li>
                    <li>Before your coin stops moving, tap the screen to unleash your burst ability</li>
                </ul>
                <h3 className={classes.details}>Details:</h3>
                <ul className={classes.details}>
                    <li>Platform: Android, Web Browser</li>
                    <li>My Roles: Gameplay Programmer, Level Designer, UI Programmer</li>
                    <li>Tools Used: Unity3D, C#</li>
                    <li>Team Size: 8 people</li>
                    <li>Time Frame: 10 weeks</li>
                </ul>
            </div>
        </div>
    );
}
